.page {
  background-color: transparent;
  border: none;
  color: white;
  font-size: 18px;
  padding: 10px 15px;
}

.page-radio {
  position: absolute;
  clip: rect(0 0 0 0);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden;
}
.page-label {
  color: white;
  font-size: 18px;
  padding: 10px 15px;
}

.page-radio:checked + .page-label {
  border: 1px solid red;
  color: green;
}
