body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  background-image: -webkit-gradient(
    linear,
    right bottom, left top,
    from(#159957),
    color-stop(#008e7e),
    color-stop(#00809b),
    color-stop(#006da5),
    to(#155799)
  );
  background-image: -webkit-linear-gradient(
    right bottom,
    #159957,
    #008e7e,
    #00809b,
    #006da5,
    #155799
  );
  background-image: linear-gradient(
    to left top,
    #159957,
    #008e7e,
    #00809b,
    #006da5,
    #155799
  );
  display: flex;
  flex-wrap: wrap;
  min-height: 100vh;
}

.animation {
  margin: 200px auto;
  width: 200px;
  text-align: center;
}

.animation > div {
  width: 50px;
  height: 50px;
  opacity: 0.6;
  background-color: #006da5;
  border-radius: 100%;
  display: inline-block;
  -webkit-animation: circle 1.5s infinite ease-in-out both;
          animation: circle 1.5s infinite ease-in-out both;
}

.animation .circle1 {
  -webkit-animation-delay: -0.5s;
          animation-delay: -0.5s;
}

.animation .circle2 {
  -webkit-animation-delay: -0.25s;
          animation-delay: -0.25s;
}

@-webkit-keyframes circle {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

@keyframes circle {
  0%,
  80%,
  100% {
    -webkit-transform: scale(0);
            transform: scale(0);
  }
  40% {
    -webkit-transform: scale(1);
            transform: scale(1);
  }
}

.character-item {
  border: 1px solid transparent;
  width: 200px;
  border-radius: 5px;
  background-color: rgba(255, 255, 255, 0.1);
  color: white; }
  .character-item img {
    border-radius: 50%;
    width: 150px; }
  .character-item p {
    line-height: 1.6; }

.all-characters {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  width: 100%; }

.error {
  color: white;
  padding-top: 100px; }

@media screen and (min-width: 530px) {
  .all-characters {
    width: calc(100% - 280px); } }

.search {
  width: 100%;
  font-size: 18px;
  color: white;
  margin-left: 10px;
  margin-right: 10px; }
  .search input[type="text"] {
    padding: 5px;
    font-size: 16px;
    border: none;
    border-radius: 10px;
    outline: 1px solid transparent; }
  .search input {
    margin: 10px 3px 10px 9px; }
  .search div {
    margin-bottom: 15px;
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 10px; }

@media screen and (min-width: 530px) {
  .search {
    width: 270px;
    margin-top: 10px;
    margin-right: 0; }
  .search input[type="text"] {
    width: 85%; } }

.page {
  background-color: transparent;
  border: none;
  color: white;
  font-size: 18px;
  padding: 10px 15px; }

.page-radio {
  position: absolute;
  clip: rect(0 0 0 0);
  padding: 0;
  border: 0;
  height: 1px;
  width: 1px;
  overflow: hidden; }

.page-label {
  color: white;
  font-size: 18px;
  padding: 10px 15px; }

.page-radio:checked + .page-label {
  border: 1px solid red;
  color: green; }

.container {
  width: 100%;
  display: flex;
  flex-direction: column;
  height: 160px;
  justify-content: space-evenly;
  align-items: center; }

.pagination {
  display: flex; }

.choose-number-characters {
  display: flex; }

.con {
  display: flex; }

